


































import { Component, Vue } from "vue-property-decorator";
import Plan from "pev2";
export const planData: any[] = ["", ""];
@Component({
  components: {
    Plan
  }
})
export default class App extends Vue {
  private planInput: string = "";
  private queryInput: string = "";
  private planSource: string = "";
  private planQuery: string = "";
  private submitPlan(): void {
    this.planQuery = this.queryInput;
    this.planSource = this.planInput;
    this.queryInput = "";
    this.planInput = "";
  }
}
